* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: $serif;
}

//MIXIN
@mixin main-text {
    color: $text;
    letter-spacing: 0.1rem;
    text-align: $center;
}

@mixin paragraph-text {
    color: $blue-gray;
    font-family: $serif;
    font-size: $font-s;
}

@mixin quote-text {
    color: $blue-gray;
    font-family: $quote;
    font-size: $font-xs;
    text-align: $center;
    font-style: italic;
}

@mixin input-text {
    color: $secondary;
    font-family: $sans-serif;
    font-size: $font-s;
}

@mixin flex {
    display: flex;
}

//TYPOGRAPHY
h1 {
    @include main-text;
    font-weight: $extra-bold;
    font-size: $font-l;
}

h2 {
    @include main-text;
    font-weight: $bold;
    font-size: $font-m;
}

h3 {
    @include main-text;
    text-align: $left;
    font-weight: normal;
}

h4 {
    @include quote-text;
}

h6 {
    @include input-text;
}

p {
    @include paragraph-text;
}

.link {
    @include paragraph-text;
    color: $secondary;
    &-button {
        @include paragraph-text;
        font-size: 1rem;
        text-decoration: none;
        border: $border;
        padding: 0.5em 1rem;
        width: fit-content;
        cursor: pointer;
        &:hover {
            background: $blush;
        }
        & a {
            all: unset;
            color: $primary;
        }
    }
}